<template>
  <div class="login">
    <h1 style="padding-bottom:50px">帐号登录</h1>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm">
      <el-form-item prop="text">
        <span class="id">帐号：</span><el-input
          type="text"
          prefix-icon="el-icon-user"
          v-model="ruleForm.text"
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
       <span class="id"> 密码：</span><el-input
          type="password"
          prefix-icon="el-icon-lock"
          v-model="ruleForm.password"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >登录</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {

    return {
      ruleForm: {
        text: "7de298fc-2bae-4afb-b20b-ca85130e4159",
        password: "123",
      },
      rules: {
        text: [{ trigger: "blur", message: "请输入帐号", required: true }],
        password: [{ trigger: "blur", message: "请输入密码", required: true }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let res 
          try {
           res = await this.$http.post("/accesstoken", { accesstoken:this.ruleForm.text });
            sessionStorage.setItem('userInfo',JSON.stringify(res))
            sessionStorage.setItem('token',this.ruleForm.text)
            this.$router.push('/')
          } catch (error) {
            alert('token错误')
          }
          console.log(res)
        }
      });
    },
  },
};
</script>
<style scoped>
.login {
  width: 450px;
  margin-top: 100px;
  position: absolute;
  left: 35%;
}

</style>